import Vue from 'vue'
import Vuex from 'vuex'
import state from './state.js' // 导入state中的导出
// state是取得变量名字，如果不是state的话不能简写，比如state123那么在store的实例里面就直接写成：state:state123
import * as mutations from './mutations.js'
import * as actions from './actions.js'

Vue.use(Vuex)
// 注册插件

export default new Vuex.Store({
  state,
  mutations,
  actions
})
