export const getBanner = ({commit},options)=>{
    axios.get('/book').then((data)=>{
        // 把请求好的数据方法vuex的state中
        // data.data时候传过来的数据
        commit('changeBanner',data.data)
    })
}
export const getHomeList = ()=>{
    // 调取首页下的列表数据
    return axios.get('https://www.easy-mock.com/mock/5cf24c737bfa3e44efc47c91/book/homelist')
}
// 获取列表页的初始数据
export const getList = ({commit},options) => {
    axios.get('https://www.easy-mock.com/mock/5cf2500f7bfa3e44efc47caa/datalist/datalist').then((data)=>{
        commit('changeList',data.data)
        console.log(data.data);
    })
}